.FilteContainer {
  background: white;
  border-radius: 20px;
  padding: 1rem;
  margin: 1rem 1rem 1rem 0;
  display: flex;
  flex-direction: column;
	justify-content: space-between;
	max-height: 680px;
  .titleWraper {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: var(--textColor);
    padding: 1rem;
    h5 {
      margin: 0 20px;
    }
  }
  label {
    color: gray;
  }
  input {
    border: none;
    box-shadow: 1px 2px 3px gray;
    padding: 0.5rem;
		margin-bottom: 1rem;
  }
}
